.item {
    border: 2px solid gray;
    padding: 15px;
    display: inline-block;
    width: 300px;
    height: 400px;
    margin: 15px;    
}
.title {
    font-size: 35px;
    text-align: center;
    color: white;
}
.img {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
}
.info {
    color: white;
    font-size: 30px;
    text-align: center;
    padding: 15px 0 0 0;
}
.info h2 {
    margin-bottom: 15px;
}
.info p {
    margin-bottom: 15px;
}
.info button {
    padding: 12px 20px;
    font-size: 20px;
}
.empty {
    font-size: 50px;
    color: white;
    text-align: center;
}